.my-button {
  font-family: "Jaldi", sans-serif;
  color: white;
  /* background-color: #ACB5BD; */
  background-color: #acb5bd;
  border-radius: 10px;
  border: 1px solid #acb5bd;
  transition: all 0.3s ease 0s;
  padding: 5px 15px 5px 15px;
  font-weight: bold;
  letter-spacing: 1px;
}

.my-button:hover {
  box-shadow: 0px 15px 20px #d0d0d0;
  transform: translateY(-7px);
}

